<template>
	<view id="register">
		<view class="title">
			注册账号
		</view>
		<view  class="content">
			
		      <el-form ref="registerFormRef" label-width="180rpx" :model="registerInfo"
			   :rules="registerFormRules" class="form">
			    <!-- 用户名-->
			   <el-form-item prop="name" label="用户名" size="medium">
			     <el-input v-model="registerInfo.name"></el-input>
			   </el-form-item>
		        <!-- 账号-->
		        <el-form-item prop="account" label="账号" size="medium">
		          <el-input v-model="registerInfo.account"></el-input>
		        </el-form-item>
		        <!-- 密码 -->
		        <el-form-item prop="password" label="密码" size="medium">
		          <el-input v-model="registerInfo.password" type="password"></el-input>
		        </el-form-item>
				
				<el-form-item prop="oldPassword" label="确认密码" size="medium">
				  <el-input v-model="registerInfo.oldPassword" type="password"></el-input>
				</el-form-item>
		      </el-form>
		</view>
		<view class="handleRegister" @click="handleRegister">
			立即注册
		</view>
	</view>
</template>

<script>
	
	import { nameTest,register } from '../../service/register.js'
	
	export default {
		data() {
			  var validatePassword = (rule, value, callback) => {
			      if (value === '') {
			        callback(new Error('请再次输入密码'))
			      } else if (value !== this.$data.registerInfo.password) {
			        callback(new Error('两次输入密码不一致!'))
			      } else {
			        callback()
			      }
			    }
				var validateName = (rule, value, callback) => {
					nameTest(this.$data.registerInfo.name).then(res => {
						if (res === '请输入用户名') {
						  callback(new Error('请输入用户名'))
						} else if (res === "用户名已存在") {
						  callback(new Error('用户名已存在'))
						} else {
						  callback()
						}
					})
					/*uni.request({
						url: `http://localhost:8000/register`, //仅为示例，并非真实接口地址。
						method:'POST',
						data:{
							name:this.$data.registerInfo.name
						},
						header: {
						'Content-Type': 'application/x-www-form-urlencoded'  
						}, 
						success: (res) => {
						console.log(res.data)
						if (res.data === '请输入用户名') {
						  callback(new Error('请输入用户名'))
						} else if (res.data == "用户名已存在") {
						  callback(new Error('用户名已存在'))
						} else {
						  callback()
						  }
						}
					})*/
				  }
			return {
			   registerInfo:{
				   name:"",
				   account:"",
				   password:"",
				   oldPasswor:""
			   },
			   registerFormRules:{
				   name: [
				    { required: true, validator: validateName, trigger: 'blur' } 
				   ],
				   account:[
				    { required: true, message: '请输入登录账号', trigger: 'blur' },
				    { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
				    ],
					 // 验证密码是否合法
				    password: [
				    { required: true, message: '请输入登录密码', trigger: 'blur' },
				    { min: 3, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
				   ],
				   oldPassword:[
					 { required: true, validator: validatePassword, trigger: 'blur' } 
				   ]
			   }
			}
		},
		methods: {
			handleRegister(){
			  /* uni.request({
			   	url: `http://localhost:8000/register`, //仅为示例，并非真实接口地址。
			   	method:'POST',
			   	data:{
			   		name:this.$data.registerInfo.name,
					account:this.$data.registerInfo.account,
					password:this.$data.registerInfo.password
			   	},
			   	header: {
			   	'Content-Type': 'application/x-www-form-urlencoded'  
			   	}, 
			   	success: (res) => {
					
				},
			  })*/
			  register(this.$data.registerInfo).then(res => {
				  console.log(res)
			  })
			}
		}
	}
</script>

<style>
@import url('element-ui/lib/theme-chalk/index.css');

.title{
	position: fixed;
	left: 50%;
	top: 10%;
	transform: translate(-50%,-50%);
	font-size: 50rpx;
}
.handleRegister{
	position: fixed;
	left: 50%;
	bottom: 5%;
	transform: translate(-50%,-50%);
	width: 90%;
	height: 100rpx;
	line-height: 100rpx;
	font-size: 40rpx;
	border: 1px solid #ccc;
	border-radius:20rpx;
	text-align: center;
    
}
.content{
	position: fixed;
	left: 50%;
	top: 48%;
	transform: translate(-50%,-50%);
	display: flex;
	flex-direction: column;
	justify-content:space-evenly;
	padding: 0 10px;
	width: 90%;
	height:800rpx;
	border: 1px solid #ccc;
	border-radius: 20px;
	box-sizing: border-box;
}
/deep/.el-form-item__label{
	font-size:30rpx;
}
/deep/.el-form-item__content{
	margin: 20rpx 0
}
/deep/.el-form-item__label{
	margin-top: 20rpx;
}
</style>
